export default {
	request(options) {
		return new Promise((resolve, reject) => {
			//
			uni.request({
				...options,
				success: res => {
					if (options.native) {
						resolve(res) //  
					}
					if (res.status === 200) {
						resolve(res.data) //
					} else {
						console.log('404');
						reject(res) //
					}
				}
			})
		})
	},
	get(url, data = {}, options = {}) {
		options.url = url;
		options.data = data;
		options.method = 'get';
		return this.request(options)
	},
	post(url, data = {}, options = {}) {
		options.url = url;
		options.data = data;
		options.method = 'post';
		return this.request(options)
	}
}
