import {
	apiResquest
} from '../utils/http.js'

/**  */
export const saveOrder = (query) => {
	return apiResquest({
		url: '/http/callback',
		method: 'POST',
		query: {
			...query
		}
	})
}

/**  */
export const saveOtherOrder = (query) => {
	return apiResquest({
		url: '/saveOtherOrder',
		method: 'POST',
		query: {
			...query
		}
	})
}

/**  */
export const getOrderList = (query) => {
	let openid = query
	return apiResquest({
		url: `/getOrderList?openid=${openid}`,
		method: 'GET'
	})
}

/**  */
export const deleteOverTimeOrder = (query) => {
	let _id = query
	return apiResquest({
		url: `/deleteOverTimeOrder?_id=${_id}`,
		method: 'GET'
	})
}

/**  */
export const seeOrderInfo = (query) => {
	let id = query
	return apiResquest({
		url: `/getOrderList/seeOrderInfo?id=${id}`,
		method: 'GET'
	})
}