import {
	config
} from '../config.js'

export const apiResquest = (prams) => { 
	//prams: API
	
	// 
	let headerData = {
		'content-type': 'application/json'
	}

	let dataObj = null
	
	// token
	if (prams.method === "GET") {
		headerData = {
			'content-type': 'application/json',
			'token': uni.getStorageSync('token')
		}
	} else {
		dataObj = {
			'data': prams.query,
			'token': uni.getStorageSync('token')
		}
	}
	return new Promise((resolve, reject) => {
		// 
		let url = config.base_url + prams.url;
		uni.showLoading({
			title: '',
			mask: true
		})
		return uni.request({
			url: url,
			data: dataObj,
			method: prams.method,
			header: headerData,
			success: (res) => {
				// console.log('')
				uni.hideLoading()
				//
				// if (res.data.status != 200) {
				// 	uni.showToast({
				// 		title: ':' + res.data.message,
				// 		duration: 1000,
				// 		icon: "none"
				// 	})
				// 	return;
				// }
				resolve(res.data);
			},
			fail: (err) => {
				// console.log('')
				reject(err);
				console.log(err)
				uni.hideLoading()
			},
			complete: () => {
				// console.log('')
				uni.hideLoading()
			}
		});
	})
}
